"use client"
import React, { useState, useEffect } from 'react';
import { Send, Mic, MicOff, VolumeX } from 'lucide-react';
import PatientSidebar from '../../components/sidebar_patient';
import CallButton from '../../components/CallButton';

// Define interfaces for messages and API response
interface Message {
  type: 'user' | 'bot';
  content: string;
  error?: boolean;
}

interface GeminiApiResponse {
  candidates: {
    content: {
      parts: { text: string }[];
    };
  }[];
}

const ChatBot: React.FC = () => {
  const [questions, setQuestions] = useState<string>('');
  const [messages, setMessages] = useState<Message[]>([]);
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [isListening, setIsListening] = useState<boolean>(false);
  const [isSpeaking, setIsSpeaking] = useState<boolean>(false);
  const [recognition, setRecognition] = useState<SpeechRecognition | null>(null);
  const [currentUtterance, setCurrentUtterance] = useState<SpeechSynthesisUtterance | null>(null);

  useEffect(() => {
    if (typeof window !== 'undefined' && (window.SpeechRecognition || window.webkitSpeechRecognition)) {
      const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
      const recognitionInstance = new SpeechRecognition();
      recognitionInstance.continuous = true;
      recognitionInstance.interimResults = true;

      recognitionInstance.onresult = (event: SpeechRecognitionEvent) => {
        const transcript = Array.from(event.results)
          .map((result: SpeechRecognitionResult) => result[0])
          .map((result: SpeechRecognitionAlternative) => result.transcript)
          .join('');
        setQuestions(transcript);
      };

      recognitionInstance.onend = () => {
        setIsListening(false);
      };

      setRecognition(recognitionInstance);
    }
  }, []);

  const stopSpeaking = () => {
    if (currentUtterance) {
      window.speechSynthesis.cancel();
      setCurrentUtterance(null);
      setIsSpeaking(false);
    }
  };

  const toggleListening = () => {
    if (isListening) {
      recognition?.stop();
      setIsListening(false);
    } else {
      stopSpeaking();
      recognition?.start();
      setIsListening(true);
    }
  };

  const speakText = (text: string) => {
    const utterance = new SpeechSynthesisUtterance(text);
    utterance.onend = () => {
      setIsSpeaking(false);
      setCurrentUtterance(null);
    };
    setCurrentUtterance(utterance);
    setIsSpeaking(true);
    window.speechSynthesis.speak(utterance);
  };

  const generateAnswer = async () => {
    try {
      setIsLoading(true);
      setMessages((prev) => [...prev, { type: 'user', content: questions }]);

      const response = await fetch(
        'https://generativelanguage.googleapis.com/v1beta/models/gemini-1.5-flash:generateContent?key=AIzaSyBXvyQXa7LjTNqqDkm3uvubhhkQ1A5dWZs',
        {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            contents: [{ parts: [{ text: questions }] }],
          }),
        }
      );

      if (!response.ok) {
        throw new Error('API request failed');
      }

      const data: GeminiApiResponse = await response.json();
      const botResponse = data.candidates[0].content.parts[0].text;

      setMessages((prev) => [...prev, { type: 'bot', content: botResponse }]);
      if (isListening) {
        speakText(botResponse);
      }
      setQuestions('');
    } catch (error) {
      console.error('Error:', error);
      const errorMessage = 'Error generating response. Please try again.';
      setMessages((prev) => [...prev, { type: 'bot', content: errorMessage, error: true }]);
      if (isListening) {
        speakText(errorMessage);
      }
    } finally {
      setIsLoading(false);
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent<HTMLTextAreaElement>) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      if (questions.trim() && !isLoading) {
        generateAnswer();
      }
    }
  };

  return (
    <div className="flex h-screen bg-gray-50">
      {/* Sidebar */}
      <PatientSidebar/>
      
      {/* Main Chat Area */}
      <div className="flex-1 flex flex-col">
        {/* Header */}
        <div className="bg-white shadow-sm p-4 flex justify-between items-center">
          <h1 className="text-xl font-semibold text-green-600">AI Assistant</h1>
          <div className="flex items-center gap-4">
            <div className="text-right">
              <p className="text-gray-700 font-medium">Are you feeling anxious? Talk to us</p>
              <CallButton />
            </div>
          </div>
        </div>
        
        {/* Chat Messages */}
        <div className="flex-1 p-6 overflow-y-auto bg-gray-50">
          {messages.length === 0 && (
            <div className="flex flex-col items-center justify-center h-full text-center">
              <div className="bg-white p-8 rounded-2xl shadow-md max-w-md">
                <h2 className="text-xl font-bold text-green-600 mb-4">Welcome to the AI Assistant</h2>
                <p className="text-gray-600 mb-6">
                  Ask me anything, and I'll do my best to help you. You can type your message or use voice input.
                </p>
                <p className="text-sm text-gray-500">This assistant is powered by Gemini AI</p>
              </div>
            </div>
          )}
          
          {messages.map((message, index) => (
            <div
              key={index}
              className={`mb-4 ${
                message.type === 'user' ? 'flex justify-end' : 'flex justify-start'
              }`}
            >
              <div
                className={`rounded-2xl p-4 max-w-xl shadow-sm ${
                  message.type === 'user'
                    ? 'bg-green-500 text-white rounded-br-none'
                    : message.error
                    ? 'bg-red-100 text-red-600 rounded-bl-none'
                    : 'bg-white text-gray-800 rounded-bl-none'
                }`}
              >
                <p className="whitespace-pre-wrap break-words">{message.content}</p>
              </div>
            </div>
          ))}
          
          {isLoading && (
            <div className="flex justify-start mb-4">
              <div className="bg-white rounded-2xl p-4 shadow-sm animate-pulse max-w-xl">
                <div className="flex gap-2">
                  <div className="w-2 h-2 bg-gray-300 rounded-full animate-bounce"></div>
                  <div className="w-2 h-2 bg-gray-300 rounded-full animate-bounce delay-75"></div>
                  <div className="w-2 h-2 bg-gray-300 rounded-full animate-bounce delay-150"></div>
                </div>
              </div>
            </div>
          )}
        </div>
        
        {/* Input Area */}
        <div className="p-4 bg-white border-t border-gray-200">
          <div className="max-w-4xl mx-auto flex gap-2">
            <textarea
              value={questions}
              onChange={(e) => setQuestions(e.target.value)}
              onKeyPress={handleKeyPress}
              placeholder="Type your message here... (Press Enter to send)"
              className="flex-1 resize-none border border-gray-200 rounded-2xl p-4 focus:outline-none focus:ring-2 focus:ring-green-500 text-gray-700 min-h-[80px]"
            />
            <div className="flex flex-col gap-2">
              <button
                onClick={toggleListening}
                className={`p-4 rounded-full ${
                  isListening
                    ? 'bg-red-500 hover:bg-red-600'
                    : 'bg-green-500 hover:bg-green-600'
                } text-white transition-colors`}
                aria-label="Toggle voice input"
              >
                {isListening ? <MicOff className="w-5 h-5" /> : <Mic className="w-5 h-5" />}
              </button>
              
              {isSpeaking && (
                <button
                  onClick={stopSpeaking}
                  className="p-4 bg-gray-500 hover:bg-gray-600 text-white rounded-full transition-colors"
                  aria-label="Stop speaking"
                >
                  <VolumeX className="w-5 h-5" />
                </button>
              )}
            </div>
          </div>
          
          <div className="max-w-4xl mx-auto mt-2">
            <button
              onClick={generateAnswer}
              disabled={isLoading || !questions.trim()}
              className="w-full bg-green-500 hover:bg-green-600 text-white rounded-2xl py-3 px-4 flex items-center justify-center gap-2 transition-colors disabled:opacity-50 disabled:cursor-not-allowed shadow-sm"
            >
              {isLoading ? (
                'Generating...'
              ) : (
                <>
                  <Send className="w-4 h-4" />
                  <span>Send Message</span>
                </>
              )}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ChatBot;